import React, { Component, PropTypes } from 'react'

export default class Posts extends Component {
  render() {
    <ul>
    {this.props.posts.map((post, i) => {
      return <li key={i}>{post.title}</li>
    })}
    </ul>
  }
}

Posts.PropTypes = {
  posts: PropTypes.array.isRequired
}


